import { gql } from '@apollo/client';

export const QUERY_PRODUCTS = gql`
 query getProducts($category: ID) {
  products(category: $category) {
    _id
    name
    description
    price
    quantity
    image
    reviews {
      _id
      user {
        _id
        firstName
        lastName
      }
      rating
      comment
    }
    category {
      _id
    }
  }
}
`;

export const QUERY_CHECKOUT = gql`
  query getCheckout($products: [ID]!) {
    checkout(products: $products) {
      session
    }
  }
`;

export const QUERY_ALL_PRODUCTS = gql`
  {
    products {
      _id
      name
      description
      price
      quantity
      category {
        name
      }
    }
  }
`;

export const QUERY_CATEGORIES = gql`
  {
    categories {
      _id
      name
    }
  }
`;

export const QUERY_USER = gql`
  {
    user {
      firstName
      lastName
      orders {
        _id
        purchaseDate
        products {
          _id
          name
          description
          price
          quantity
          image
        }
      }
    }
  }
`;
export const QUERY_ME = gql`
{
me {
firstName
lastName
role
email
password
}
}`;
export const QUERY_PRODUCT = gql`
query Product($id: ID!) {
product(_id: $id) {
_id
name
description
image
quantity
price
category {
    _id
    name
         }
reviews  {
user {
  _id
  firstName
  lastName
  email
  password
  role
     }
         }
  }
}`